<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="销售组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" @change="companyChange" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue1" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @change="changeSuppliter" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="厂家鉴定单据号" size="mini">
            <el-input v-model="params.factory_appraisal_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select v-model="params.auditor_status" clearable>
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @focus="DepartmentListInvok" @change="departmentChange" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="胎号" size="mini">
            <el-input v-model="params.tire_un" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="salesAddF" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button @click="examineFun" size="mini" icon="iconfont icon-pass" type="primary" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="examineFunF" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="abolishFun" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" max-height="600" style="width: 100%" @selection-change="handleSelectionChange" v-loading="loading1">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column label="操作" fixed="right" width="100">
            <template slot-scope="scope">
              <el-button @click="seeDetail(scope, scope.row)" type="text" size="small" v-if="authority.b_info==1">
                查看
              </el-button>
              <el-button @click="handleEdit(scope, scope.row)" type="text" size="small" v-if="scope.row.auditor_status=='1'&&authority.b_edit==1">
                修改
              </el-button>

              <el-button disabled type="text" size="small" v-else>
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="factory_appraisal_sn" width="180" label="厂家鉴定单据号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="supplier_name" width="150" label="供应商(厂家)">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="num" width="150" label="返胎数量">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" width="150" label="操作员">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="180" label="鉴定时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" width="200" label="单据时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_time" width="150" label="审核时间">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="160" label="审核状态">
          </el-table-column>

          <el-table-column :show-overflow-tooltip="true" property="department_name" width="100" label="部门">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" width="150" label="备注">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import { getData, opthionList } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleFactoryjudgementDefault",
  data() {
    return {
      companyList: [],
      companys: {
        options: []
      },
      timeValue1: [],
      departments: {
        options: []
      },
      loading:false,
      SelesTomer: {
        options: []
      }, //客户列表
      params: {
        auditor_status: 0
      },
      dialogFormVisible: false
    };
  },
  computed: {
    ...mapGetters(["factoryjudgementDefault_Get"])
  },
  methods: {
    ...mapActions([
      "factoryjudgementAdd",
      "factoryjudgementDefault",
      "factoryjudgementExamine"
    ]),
    companyChange(val) {
      if (val) {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "department_id", ""); // 初始化部门下拉选中值
      this.$set(this.params, "supplier_id", ""); // 初始化供应上下拉选中值
      // getDepartmentListCommon(this, this.params, 1);
    },
    departmentChange(val) {
      if (val) {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    supplierChange(val) {
      // let options = {
      //   company_id: this.params.company_id,
      //   supplier_name: val
      // };
      // supplierListOpen(this, options);
      this.supplierListInvok(val);
    },
    changeSuppliter(val) {
      if (val) {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    seeDetail(scope, row) {
      this.factoryjudgementExamine({
        // moduleName: "factoryjudgementExamine",
        factory_appraisal_id: row.factory_appraisal_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/factoryjudgement/factoryjudgement_examine"
      });
    },
    examineFun() {
      //审核
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要审核的信息！",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_appraisal_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "factory_appraisal_id",
        eachData: {
          factory_appraisal_id: creditArr
        },
        examineApi: "auditorPassCompanyAppraisal",
        list: {
          UrlApi: "getSupplierAppraisalList",
          data: this.TableData,
          esData: {
            auditor_status: 0
          }
        }
      };
      let This = this;
      this.$common.examineFun(funcan, function() {
        getData(This.searchParams);
      });
    },
    examineFunF() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息！",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_appraisal_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "factory_appraisal_id",
        eachData: {
          factory_appraisal_id: creditArr
        },
        examineApi: "auidtorNoPassCompanyAppraisal",
        list: {
          UrlApi: "getSupplierAppraisalList",
          data: this.TableData
        }
      };
      let This = this;
      this.$common.examineFun(funcan, function() {
        getData(This.searchParams);
      });
    },
    abolishFun() {
      //作废
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要作废的信息！",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].factory_appraisal_id);
      }
      var creditArr = addr.toString();
      var funcan = {
        examineID: "factory_appraisal_id",
        eachData: {
          factory_appraisal_id: creditArr
        },
        examineApi: "auditorInvalidCompanyAppraisal",
        list: {
          UrlApi: "getSupplierAppraisalList",
          data: this.TableData,
          esData: {
            auditor_status: 0
          }
        }
      };
      let This = this;
      this.$common.examineFun(funcan, function() {
        getData(This.searchParams);
      });
    },
    salesModifyF(scope, row) {},
    salesAddF() {
      this.factoryjudgementAdd({
        // moduleName: "factoryjudgementAdd",
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/factoryjudgement/factoryjudgement_add"
      });
    },
    handleEdit(scope, row) {
      // infoCompanyAppraisal
      this.factoryjudgementAdd({
        // moduleName: "factoryjudgementAdd",
        factory_appraisal_id: row.factory_appraisal_id,
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
      this.$router.push({
        path: "/aftersale/factoryjudgement/factoryjudgement_add"
      });
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});
      this.timeValue = [];
      this.timeValue1 = [];
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getSupplierAppraisalList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
      // wz cache
      this.factoryjudgementDefault({
        params: {},
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (this.timeValue1) {
        this.params.auditor_start_time = this.timeValue1[0];
        this.params.auditor_end_time = this.timeValue1[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: this.goPage
      });
      getData(this.searchParams);
      // wz cache
      this.factoryjudgementDefault({
        params: this.params,
        goPage: this.goPage,
        pageSize: this.pageSize
      });
    },
    //删除
    deletes() {
      var deledata = {
        deleID: "factory_appraisal_id",
        deleSelect: this.multipleSelection,
        deleApi: "delCompanyAppraisal",
        list: {
          UrlApi: "getSupplierAppraisalList",
          data: this.TableData,
          esData: Object.assign({}, this.params)
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams);
    }
  },
  mounted() {
    let wz_search_params = {};
    if (
      this.factoryjudgementDefault_Get &&
      this.factoryjudgementDefault_Get.params
    ) {
      if (this.factoryjudgementDefault_Get.params.start_time) {
        const {
          start_time,
          end_time
        } = this.factoryjudgementDefault_Get.params;
        this.timeValue = this.timeValue.concat([start_time, end_time]);
      }
      if (this.factoryjudgementDefault_Get.params.auditor_start_time) {
        const {
          auditor_start_time,
          auditor_end_time
        } = this.factoryjudgementDefault_Get.params;
        this.timeValue1 = this.timeValue1.concat([
          auditor_start_time,
          auditor_end_time
        ]);
      }
      this.$set(this, "params", this.factoryjudgementDefault_Get.params);
      wz_search_params = this.params;
      opthionList(this, this.factoryjudgementDefault_Get.params);
    }
    if (
      this.factoryjudgementDefault_Get &&
      this.factoryjudgementDefault_Get.goPage
    ) {
      this.params.page = this.factoryjudgementDefault_Get.goPage;
      this.params.per_page = this.factoryjudgementDefault_Get.pageSize;
    }
    this.$set(this, "searchParams", {
      params: wz_search_params, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getSupplierAppraisalList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams); //获取销售订单列表
  }
};
</script>

<style scoped>
